.orengeCircle {
  background-color: #ed8119;
  width: 160px;
  border-radius: 50%;
  padding: 15px;
  text-align: center;
  margin: 3px auto;

}
.orengeCircleImg {
  border: 3px solid #fff;
  border-radius: 50%;
  height: 130px;
  width: 130px;

}

.chooseBtn {
  color: #000;
  border: 2px solid #f48d2a;
  background-color: #fff;
  padding: 8px 20px;
}

.btnPrimary{
    text-transform: none;
    width: 100%;
letter-spacing: 0.25px;
border: 2px solid #fff !important;
margin-top: 13px;
background-color: rgb(242,123,0) !important;
border-radius: 10px !important;
}
.footer{
    display: flex;
flex-wrap: wrap;
flex-shrink: 0;
align-items: center;
justify-content: flex-end;
padding: .75rem;
border-top: 1px solid #dee2e6;
border-bottom-right-radius: calc(.3rem - 1px);
border-bottom-left-radius: calc(.3rem - 1px);
}