.dashboard{
    color: #fff;
    padding-top: 5px;
    margin: 0 0 0 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    }
    .darkBlue{
        background-color: #013E87;
    }
    .bgLightWhite{
        background-color: #e2f0ffcc;
    border-radius: 20px;
    padding: 15px;
    }
    .btnRight{
        text-align: right !important;
    }
    .dashBoardBtn{
    color: #013e87;
    border: 2px solid #fff !important;
    background-color: rgb(242,123,0) !important;
    border-radius: 3px !important;
    }
    .refreshBtn{
    background-color: #F48D2A;
    margin: 0px;
    text-align: center;
    color: #fff;
    border: none;
    border-radius: 2px;
    }
    .searchBtn{
    background-color: #013e87;
    right: 50px;
    text-align: center;
    color: #fff;
    border: none;
    border-radius: 2px;
    }
    .searchBtn:active{
    color: #fff;
    background-color:none;
    border-color: none;
    
    }
    .searchBtn:hover:active{
        box-shadow: none;
    }
    .refreshBtn:hover:active{
        box-shadow: none;
    }
    