.navBar{
    background-color: #00000029;
border-bottom: 5px solid #0000000a;
}
.logout {
    padding: 10px;
    background-color: #F48D2A;
    margin: 0px 16px;
    text-align: center;
    color: #fff;
    border: none;
    border-radius: 5px;
    margin-left: 30px;
}
.logout:hover {
    padding: 10px;
    background-color: #ffffff;
    margin: 0px 16px;
    text-align: center;
    color: #000;
    margin-left: 30px;
}
.socialIconStyle{
        color: #fff;
        background-color: #00000080;
        margin: 5px;
        padding: 10px 12px;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        line-height: 20px;
        text-decoration: none;
        position: relative;
        display: inline-block;
}
.socialIconStyle:hover{
    background: #f38b21;
}
.badge{
position: absolute;
height: 20px;
width: 20px;
top: -5px;
right: -6px;
border-radius: 50%;
background: red !important;
font-size: .75em;
font-weight: 700;
color: #fff;
display: flex;
text-align: center;
justify-content: center;
}

.HeaderEnd{
    text-align: right;
}
.userImg{
    object-fit: fill;
    width: 40px;
    height: 40px;
    border-radius:50% ;
    border: 2px solid #fff;

}
.userImg:before {
    display: inline-block;
    content: "\f0d7";
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    background-color: #00317a;
}