.orangeBgNew {
  background-color: #f48d2a;
  border-radius: 20px;
  vertical-align: middle;
  height: 100%;
  padding-top: 1px;
}
.orengeCircle {
  background-color: #ed8119;
  width: 160px;
  border-radius: 50%;
  padding: 15px;
  text-align: center;
  margin: 3px auto;

}
.orengeCircleImg {
  border: 3px solid #fff;
  border-radius: 50%;
  height: 130px;
  width: 130px;
}
.blueBgNew {
  background-color: #013e87;
  border-radius: 3px;
  margin: 10px;
  padding: 8px;
  position: relative;
}
.blueBgNewP {
  margin: 0;
  color: #fff;
  text-align: center;
}
.blueBgIcon {
  background-color: #032752;
  position: absolute;
  right: 0px;
  color: #fff;
  height: 40px;
  max-width: 40px;
  margin-top: -8px;
  border-radius: 3px;


}
.blueBgIconButton {
  background-color: #032752;
  border: none;
}
.blueBgIconButton:hover {
  background-color: #032752;
}
.blueBgIconButton:active {
  background-color: #032752;
}
.blueBgIconButton:focus {
  background-color: #032752;
  border-color: initial;
  box-shadow: none;
}

.imgMargin {
    width: 160px;
}
.navButton {
  width: 100%;
  color: #ffffff9e;
  text-align: center;
  border-color: transparent !important;
  border-left: 5px solid transparent !important;
  height: 100px;
  align-items: center;
}
.navButton:hover{
    color: #ffffff9e;
}
.navButtonXs {
  color: #ffffff9e !important;
  text-align: center;
  border-color: transparent !important;
  border: 2px solid #ea8523 !important;
  font-size: 12px !important;
  font-weight: 400;
  border-radius: 5px !important;
  padding: 3px;
  margin: 4px 0px;
  line-height: 16px;
}
.navButtonActive {
  color: #fff !important;
  background-color: #e47a14 !important;
  border-left: 5px solid #b7610f !important;
  border-radius: 0px !important;
  font-weight: 600;
  position: relative;
  text-align: center;
  line-height: 16px;
}
.navButtonActiveXs {
  color: #fff !important;
  background-color: #e47a14 !important;
  border-left: 0px solid transparent !important;
  border-bottom: 2px solid #b7610f !important;
  border-right: 0px solid transparent !important;
  border-radius: 5px !important;
  font-weight: 400;
  position: relative;
  width: 100px;
  padding: 3px;
  margin: 4px 0px;
  line-height: 16px;
}

.navButton .active {
  color: #fff !important;
  background-color: #e47a14 !important;
  border-left: 5px solid #b7610f !important;
  border-radius: 0px !important;
  font-weight: 600;
  position: relative;
  text-align: center;
}

.navButtonActive:after {
  content: "";
  position: absolute;
  top: 40%;
  right: -15px;
  height: 0px;
  width: 0px;
  border-top: solid 8px transparent;
  border-left: solid 16px #e47a14;
  border-bottom: solid 8px transparent;
}

.navButtonIcon {
  font-size: 40px;
  padding: 10px;
}
.bottomNavbar {
  background-color: #f48d2a !important;
}
