
.chatFormHead{
    background-color: #013e87;
border-radius: 20px 20px 0px 0px;
text-align: center;
}
.chatFormBody{
    background-image: linear-gradient(to bottom right,#b8cde7 0%,#fff 100%);
    height: 70vh;
}
.rightSideChat{
    text-align: right;
}
.leftSideChat{
    text-align: left;
}
.chatText{
background-color: #f9fcff24;
color: #000;
padding: 15px;
border-radius: 15px;
background-size: cover;
display: inline-block;
box-shadow: 0 .5rem 1rem rgba(0,0,0,.15) !important;
margin-bottom: 0;
}
.chatTextImage{
 border-radius: 50%;
height: 40px;
width: 40px;
margin: 10px;
border: 3px solid #ffffffa1;
}
.chatTextTime{
    color: #a9a9a9;
}
.darkOrenge{
    background-color: #F48D2A;
    text-align: center;
}

.btnSend{
right: -8px;
background-color: #013e87;
color: #fff;
border-radius: 5px;
border: 2px solid #fff;
}
.bedge{
    /* width: 200px !important; */
    background-color: transparent !important;
    position: absolute;
    border: 1px solid #fff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    bottom: 0px;
    text-shadow: 1px 1px 4px #013e87, 0 0 25px blue, 0 0 5px darkblue;
    
     /* box-shadow: 2px 4px 4px #F48D2A inset; */
}