.chatSupport{
    color: #fff;
padding-top: 5px;
margin: 0 0 0 10px;
display: flex;
flex-direction: row;
justify-content: space-between;
}
.bgLightWhite{
    background-color: #e2f0ffcc;
border-radius: 20px;
padding: 15px;
}
.darkBlue{
    background-color: #013E87;
}
.chat{
    color: #fff;
padding-top: 5px;
margin: 0px 0 16px 10px;
}
.navList{
color: #000 !important;
cursor: pointer;
border-color: transparent !important;
border-left-color: transparent;
border-left: 5px solid transparent !important;
margin: 5px 0px;
background-color: #fff !important;

}
.navListActive {
background-color: #F48D2A !important;
}
.chatFormHead{
    background-color: #013e87;
border-radius: 20px 20px 0px 0px;
text-align: center;
}
.chatFormBody{
    background-image: linear-gradient(to bottom right,#b8cde7 0%,#fff 100%);
}
.rightSideChat{
    text-align: right;
}
.leftSideChat{
    text-align: left;
}
.chatText{
background-color: #f9fcff24;
color: #000;
padding: 15px;
border-radius: 15px;
background-size: cover;
display: inline-block;
box-shadow: 0 .5rem 1rem rgba(0,0,0,.15) !important;
margin-bottom: 0;
}
.chatTextImage{
 border-radius: 50%;
height: 40px;
width: 40px;
margin: 10px;
border: 3px solid #ffffffa1;
}
.chatTextTime{
    color: #a9a9a9;
}
.darkOrenge{
    background-color: #F48D2A;
    text-align: center;
}

.btnSend{
background-color: #013e87;
color: #fff;
padding: 4px 18px;
margin-top: 4px;
border-radius: 5px;
border: 2px solid #fff;
width: 88%;
text-align: center !important;
justify-content: center;
}