.dashboard {
  color: #fff;
  padding-top: 5px;
  margin: 0 0 0 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.darkBlue {
  background-color: #013e87;
}
.bgLightWhite {
  background-color: #e2f0ffcc;
  border-radius: 20px;
  padding: 15px;
}
.btnRight {
  text-align: right !important;
}
.dashBoardBtn {
  color: #013e87;
  border: 2px solid #fff !important;
  background-color: rgb(242, 123, 0) !important;
  border-radius: 3px !important;
}
.refreshBtn {
  background-color: #f48d2a;
  margin: 0px;
  text-align: center;
  color: #fff;
  border: none;
  border-radius: 2px;
}
.searchBtn {
  background-color: #013e87;
  right: 50px;
  text-align: center;
  color: #fff;
  border: none;
  border-radius: 2px;
}
.searchBtn:active {
  color: #fff;
  background-color: none;
  border-color: none;
}
.searchBtn:hover:active {
  box-shadow: none;
}
.refreshBtn:hover:active {
  box-shadow: none;
}
.tabBtn {
  color: #797979;
  position: relative;
  border-color: transparent !important;
  border-left: 5px solid transparent !important;
  margin-top: 20px;
  min-height: 155px;
  padding: 15px !important;
  background-color: #e2f0ff !important;
  border-radius: 15px !important;
  width: 140px;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
  text-align: center;
}
.tabBtn:hover{
  color: #797979;
    
}
.tabBtnActive {
  color: #fff !important;
  background-color: #f48d2a !important;
}
.tabBtnActive:after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  right: 47%;
  top: 100%;
  transform: rotate(90deg);
  border-style: solid;
  border-width: 7px 0 7px 15px;
  border-color: transparent transparent transparent #f48d2a;
  z-index: 2;
}
.tabNav {
  justify-content: space-around;
}
